import React from "react";
import "./Scorecard.css";
import "tachyons/src/_clears.css";

const Scorecard = props => {
  return (
    <div>
      {props.players &&
        props.players.map(player => {
          return (
            <div
              key={player.key}
              onClick={() => props.makeActive(player.key)}
              className={player.turn ? "active" : ""}
            >
              {player.name}
              <div className="score-container cf">
                {player.scores &&
                  player.scores.map((score, i) => {
                    return <div key={i}>{score}</div>;
                  })}
              </div>
            </div>
          );
        })}
    </div>
  );
};

export default Scorecard;



